html{
  font-family: 'Roboto';
}
body{
  margin: 0;
  background-color: rgb(193, 193, 255);
}
header{
  background-color: rgb(92, 89, 255);
  border-bottom: 3px solid rgb(0, 0, 255);
  border-radius: 0px 0px 10px 10px;
  padding: 15px;
}

h1{
  margin: 0;
  color: white;
  text-align: center;
  text-shadow: 0px 0px 5px black;
}

#group-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 5% 25px 0 25px;
  background-color: rgb(92, 89, 255);
  border-bottom: 3px solid rgb(0, 0, 255);
  border-radius: 10px;
  padding: 15px 10%;
}

.word-container{
  padding: 0 10px;
  background-color: rgb(193, 193, 255);
  min-width: 100px;
  text-align: center;
  border-radius: 10px;
  border-bottom: 2px solid blue;
}

#button{
  font-weight: 900;
  font-size: 1.5em;
  background-color: blue;
  padding: 15px 5px;
  color: white;
  border-radius: 10px;
  min-width: 100px;
  max-width: 200px;
  text-align: center;
  margin: 5% auto;
  border-bottom: 3px solid blue;
  cursor: pointer;
  transform: translateY(0px);
  transition: ease-in-out 200ms;
}
#button:hover{
  background-color: rgb(92, 89, 255);
  border-bottom: 5px solid blue;
  transform: translateY(5px);
  transition: ease-in-out 200ms;
}